<template>
  <div class="container py-5">
    <!-- AdminNav Component -->
    <AdminNav />

    <table class="table">
      <thead class="thead-dark">
        <tr>
          <th scope="col">
            #
          </th>
          <th scope="col">
            Email
          </th>
          <th scope="col">
            Role
          </th>
          <th scope="col" width="140">
            Action
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id">
          <th scope="row">
            {{ user.id }}
          </th>
          <td>{{ user.email }}</td>
          <td>{{user.isAdmin ? 'admin' : 'user'}}</td>
          <td v-if="user.id !== currentUser.id">
            <button @click.stop.prevent="toggleUserRole({userId: user.id, isAdmin: user.isAdmin})" type="button" class="btn btn-link">
              set as {{!user.isAdmin ? 'admin' : 'user'}}
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import AdminNav from '../components/AdminNav.vue'
import adminAPI from './../apis/admin'
import { Toast } from './../utils/helpers'
import { mapState } from 'vuex'

export default {
  components: {
    AdminNav
  },
  data() {
    return {
      users: []
    }
  },
  computed: {
    ...mapState(['currentUser', 'isAuthenticated'])
  },
  methods: {
    async fetchUser() {
      try {
        const response = await adminAPI.users.get()
        if(response.statusText !== 'OK') throw new Error(response.statusText)

        this.users = response.data.users
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: 'Unable to get users data, Please try again'
        })
      }
    },
    async toggleUserRole({userId, isAdmin}) {
      try {
        const response = await adminAPI.users.update({ userId, isAdmin: `${!isAdmin}` })
        if(response.statusText !== 'OK') throw new Error(response.statusText)

        this.users = this.users.map(user => {
          if (user.id === userId) {
            return {
              ...user,
              isAdmin: !user.isAdmin
            }
          }
  
          return user
        })
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: 'Unable to update users role, Please try again'
        })
      }
    }
  },
  created() {
    this.fetchUser()
  }
}
</script>